* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: #ddd;
  font-family: Arial;
}

.header {
  text-align: center;
  padding: 32px;
}

.row {
  display: -ms-flexbox; /* IE10 */
  display: flex;
  -ms-flex-wrap: wrap; /* IE10 */
  flex-wrap: wrap;
  padding: 0 4px;
}

/* Create four equal columns that sits next to each other */
.column {
  -ms-flex: 25%; /* IE10 */
  flex: 25%;
  max-width: 25%;
  padding: 0 4px;

  img {
    margin-top: 8px;
    vertical-align: middle;
    width: 100%;
  }
}

/* Responsive layout - makes a two column-layout instead of four columns */
@media screen and (max-width: 800px) {
  .column {
    -ms-flex: 50%;
    flex: 50%;
    max-width: 50%;
  }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column {
    -ms-flex: 100%;
    flex: 100%;
    max-width: 100%;
  }
}

.input-wrap {
  height: 50vh;
  width: 30%;
  min-width: 300px;
  margin: auto;
  transition: all .5s;
  
  input {
    width: 100%;
    height: 3rem;
    font-size: 1em;
    bottom: 0;
    margin: auto;
    margin-bottom: 0;
    padding-left: 1rem;
  }
  
  &.focused {
    width: 100%;
    height: 5rem;
    min-height: 60px;
    position: fixed;
    background: #ddd;
    top: 0;

    input {
      margin-bottom: auto;
    }
  }
}
